import React, { useEffect } from "react";
import aboutimage from "../Images/IMG-20191227-WA0023.jpg";
import { Grid } from "@material-ui/core";
import "./About.css";
import Typewriter from "typewriter-effect";
import Aos from "aos";
import "aos/dist/aos.css";
function About() {
  useEffect(() => {
    Aos.init({
      duration: 3000,
      delay: 300,
    });
  }, []);
  return (
    <div id="sec1" className="About_sec">
      {" "}
      <div>
        <div style={{ fontSize: 40 }}>
          <Typewriter
            options={{
              strings: ["About"],
              cursor: "",
              delay: 400,
              autoStart: true,
              pauseFor: 10000000000,
              loop: true,
            }}
          />
        </div>
        <Grid container xs={12} spacing={1} item>
          <Grid
            container
            item
            lg={6}
            sm={6}
            xs={12}
            direction="row"
            justify="center"
            alignItems="center"
          >
            <img
              data-aos="slide-right"
              src={aboutimage}
              className="img-fluid"
              style={{ height: "50vh" }}
            />
          </Grid>
          <Grid
            item
            container
            lg={6}
            sm={6}
            xs={12}
            style={{ fontSize: 20, textAlign: "justify", lineHeight: "100%" }}
          >
            <Typewriter
              options={{
                strings: [
                  " In aliquip dolore anim cupidatat deserunt non nisi adipisicin  ipsum officia ea do cupidatat. Dolore duis reprehenderit dolornulla deserunt Lorem id ex cupidatat aute velit. Irure incididun do excepteur laborum excepteur culpa officia deserunt voluptat    esse incididunt adipisicing enim. Dolore irure ex sit velit fugiat pariatur ullamco ad amet eiusmod fugiat culpa voluptate. Quisadipisicing cupidatat tempor duis minim aliqua ad offici consectetur ipsum tempor est id consequat. Aute cillum magnareprehenderit voluptate. Pariatur consectetur deserunt fugiat enim ex elit minim veniam culpa qui id magna labore veniam.  ",
                ],
                delay: 10,
                autoStart: true,
                pauseFor: 10000000000,
                loop: true,
              }}
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

export default About;
