import React, { useEffect } from "react";
import "./Homepage.css";
import { Grid } from "@material-ui/core";
import Typewriter from "typewriter-effect";
import Aos from "aos";
import "aos/dist/aos.css";
import Music from "./Music";
function Homepage() {
  useEffect(() => {
    Aos.init({
      duration: 2000,
      // delay: 500,
    });
  }, []);
  return (
    <div>
      <div className="banner">
        <Grid xs={12} spacing={0} item container className="Gridbox">
          <Grid
            item
            lg={6}
            sm={12}
            xs={12}
            justify="center"
            alignItems="center"
          ></Grid>
          <Grid
            item
            lg={6}
            sm={12}
            xs={12}
            style={{
              backgroundColor: "transparent",
            }}
          >
            <text
              data-aos="slide-right"
              style={{
                backgroundColor: "transparent",
                color: "white",
                overflowX: "hidden",
                overflowY: "hidden",

                fontSize: window.screen.width < 600 ? "70px" : "100px",
              }}
            >
              {" "}
              <span> Ajay</span> Tiwari
            </text>
            <Music />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
export default Homepage;
